/*
Other
*/

@import 'variables';
@import 'fonts';
/*
Components
*/
@import './components/other';
@import './components/links';
@import './components/header';
@import './components/footer';
/*
Page Styles
*/
@import './pagestyles/product';
@import './pagestyles/category';
@import './pagestyles/landing';
@import './pagestyles/contact';
*,*::before,*::after{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    line-height: 1;
}
.container{
    max-width: $container;
    margin: 0 auto;
    padding: 0px 15px;
}
ul{
    list-style: none;
}
body{
    padding-top: 100px;
}

.custom-select-header {
    font-family: 'Roboto', sans-serif !important;
}
.product-option svg {
    max-width: 270px;
}
.products__slider__info h4 {
    display: block !important;
}
.header__mob__menu--first {
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header__menu--first__bottom {
    margin-top: 40px;
}
.contact__info a span svg {
    min-width: 52px;
}

/*
Media Queries
*/
@import './components/media';

